import React, {useState} from "react";
import "./SearchOptions.css";
import "../../variables.css";

export default function SearchOptions() {
    return (
        <div className="search-options-wrapper">
            <div className="search-options-title">Search Tags</div>
            <ul className="options-list">
                <div className="option-category">
                    Color
                    <SearchOption name="Black & White" uid="black-and-white" />
                    <SearchOption name="Color" uid="color" />
                </div>

                <div className="option-category">
                    Setting
                    <SearchOption name="Nature" uid="nature" />
                    <SearchOption name="Street" uid="street" />
                </div>


            </ul>
        </div>
    )
}

function SearchOption({ name, uid }) {
    let [state, setState] = useState(false);
    return (
        <li className="option-list-entry" onClick={() => setState(!state)}>
            <div className={"checkbox-label " + (state ? "on" : "off")}>
                <label className="checkbox-symbol"></label>
                <div>{name}</div>
            </div>
        </li>
    )




}